




































































































































































































































import { Vue, Component } from 'vue-property-decorator';
import { AxiosResponse } from 'node_modules/axios';

import { ServiceRegex, ServiceDialog, ServiceToast, ServiceServerConfig, ServiceResponseFilter } from '@/../helpers';

import { ServerApiPath, WebPagePath } from '@/config';
import { ModelsForgetPpassword as Model } from './models';

import Card from '@/../components/card/card.vue';
import Step from '@/../components/step/step.vue';
import Loading from '@/../components/loading/loading.vue';
import FormPassword from '@/../components/form/form_password.vue';

@Component({
    components: { Step, Card, FormPassword },
})
export default class VuePageClass extends Vue {
    //#region Variables
    private multipleStepItem = {
        templateKey: 'forget',
        labels: [this.$i18n.ForgetPassword_Step1, this.$i18n.ForgetPassword_Step2, this.$i18n.ForgetPassword_Step3, this.$i18n.ForgetPassword_Step4],
        // shape: EStepShape.circle,
        // barColor: '#537895',
    };

    private stepCotrolItem: Model.IStepCotrolItem = {
        currentStep: 0,
        showCancel: true,
        showPrev: false,
        showNext: true,
        showReturnLogin: false,
        disablePrev: true,
        disableNext: true,
        disableSendVerifyCode: true,
    };

    private formDataOriginal: Model.IFormData = {
        account: '',
        email: '',
        verification: '',
        password: '',
        confirmPassword: '',
    };
    private formData: Model.IFormData = { ...this.formDataOriginal };

    private formDataState: Model.IFormDataState = {
        account: null,
        email: null,
        emailFormat: null,
        emailState: null,
        verification: null,
        password: null,
        confirmPassword: null,
    };

    private formButtonDisabled: Model.IFormButtonDisabled = {
        password: true,
        confirmPassword: true,
    };

    private loading = new Loading();
    //#endregion Variables

    //#region check can go to next step
    private checkButton() {
        switch (this.stepCotrolItem.currentStep) {
            case 0:
                this.stepCotrolItem.showCancel = true;
                this.stepCotrolItem.showPrev = false;
                this.stepCotrolItem.showNext = true;
                this.stepCotrolItem.showReturnLogin = false;

                this.stepCotrolItem.disablePrev = true;
                this.stepCotrolItem.disableNext = true;
                this.stepCotrolItem.disableSendVerifyCode = true;

                if (this.formData.account !== '' && ServiceRegex.isEmail(this.formData.email)) {
                    this.stepCotrolItem.disableSendVerifyCode = false;
                    this.stepCotrolItem.disableNext = false;
                }

                break;
            case 1:
                this.stepCotrolItem.showCancel = true;
                this.stepCotrolItem.showPrev = true;
                this.stepCotrolItem.showNext = true;
                this.stepCotrolItem.showReturnLogin = false;

                this.stepCotrolItem.disablePrev = false;
                this.stepCotrolItem.disableNext = true;

                if (this.formData.verification) {
                    this.stepCotrolItem.disableNext = false;
                }
                break;
            case 2:
                this.stepCotrolItem.showCancel = true;
                this.stepCotrolItem.showPrev = true;
                this.stepCotrolItem.showNext = true;
                this.stepCotrolItem.showReturnLogin = false;

                this.stepCotrolItem.disablePrev = false;
                this.stepCotrolItem.disableNext = true;

                this.stepCotrolItem.disableNext = this.formButtonDisabled.password || this.formButtonDisabled.confirmPassword;

                break;
            case 3:
                this.stepCotrolItem.showCancel = false;
                this.stepCotrolItem.showPrev = false;
                this.stepCotrolItem.showNext = false;
                this.stepCotrolItem.showReturnLogin = true;

                this.stepCotrolItem.disablePrev = true;
                this.stepCotrolItem.disableNext = true;
        }
    }

    private clickNextStep() {
        switch (this.stepCotrolItem.currentStep) {
            case 0:
                this.submitStep1();
                break;
            case 1:
                this.submitStep2();
                break;
            case 2:
                this.submitStep3();
                break;
        }
    }

    private prevStep() {
        let stepRef: any = this.$refs.step;
        if (stepRef) {
            stepRef.prevStep();
            this.stepCotrolItem.currentStep--;
            this.checkButton();
        }
    }

    private nextStep() {
        let stepRef: any = this.$refs.step;
        if (stepRef) {
            stepRef.nextStep();
            this.stepCotrolItem.currentStep++;
            this.checkButton();
        }
    }

    private pageToLogin() {
        this.$router.push(WebPagePath.Login);
    }

    private stepComplete() {}
    //#endregion

    //#region Form Data
    private formDataChange(event: any, key: string) {
        switch (key) {
            case 'account':
                this.formDataState.account = !!this.formData.account;
                break;

            case 'email':
                if (!this.formData.email) {
                    this.formDataState.emailState = false;
                    this.formDataState.email = false;
                    this.formDataState.emailFormat = true;
                } else {
                    this.formDataState.emailFormat = ServiceRegex.isEmail(this.formData.email);
                    if (!this.formDataState.emailFormat) {
                        this.formDataState.emailState = false;
                        this.formDataState.email = true;
                    } else {
                        this.formDataState.emailState = true;
                        this.formDataState.email = true;
                        this.formDataState.emailFormat = true;
                    }
                }
                break;

            case 'verification':
                this.formDataState.verification = !!this.formData.verification;
                break;
        }

        if (!this.formData.account && ServiceRegex.isEmail(this.formData.email)) {
            this.stepCotrolItem.disableSendVerifyCode = false;
        }
        this.checkButton();
    }

    private formDataClear() {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private formConfigValues(value: any, disabledButton: boolean, id: string): void {
        if (this.formData.hasOwnProperty(id)) this.formData[id] = value;
        if (this.formButtonDisabled.hasOwnProperty(id)) this.formButtonDisabled[id] = disabledButton;

        this.checkButton();
    }
    //#endreg
    //#endregion Form Data

    //#region API
    private async submitStep1() {
        // if (this.formData.account === 'Admin') {
        //     ServiceDialog.ErrorConfirmOnly(this, this.$i18n.ForgotPassword_NotAllowAdmin, this.$i18n.Status_Error);
        //     return false;
        // }

        this.loading.show();
        const url: string = ServiceServerConfig.url(ServerApiPath.ForgetPasswordStep1_C);

        interface IDataSaveParam {
            username: string;
            email: string;
        }

        const param: IDataSaveParam = {
            username: this.formData.account,
            email: this.formData.email,
        };

        try {
            // let { status, data }: AxiosResponse = await this.axios.post(url, param);
            const status = 200;
            if (status === 200) {
                ServiceToast.success(this.$i18n.ForgetPassword_Step1Success);
                this.nextStep();
            }
        } catch (e) {
            ServiceResponseFilter.catchError(this, e);
        } finally {
            this.loading.hide();
        }
    }

    private async submitStep2(): Promise<void> {
        const url: string = ServiceServerConfig.url(ServerApiPath.ForgetPasswordStep2_C);

        interface IDataSaveParam {
            username: string;
            verification: string;
        }

        const param: IDataSaveParam = {
            username: this.formData.account,
            verification: this.formData.verification,
        };

        this.loading.show();

        try {
            // let { status, data }: AxiosResponse = await this.axios.post(url, param);
            const status = 200;
            if (status === 200) {
                ServiceToast.success(this.$i18n.ForgetPassword_Step2Success);
                this.nextStep();
            }
        } catch (e) {
            ServiceResponseFilter.catchError(this, e);
        } finally {
            this.loading.hide();
        }
    }

    private async submitStep3(): Promise<void> {
        const url: string = ServiceServerConfig.url(ServerApiPath.ForgetPasswordStep3_C);

        interface IDataSaveParam {
            username: string;
            password: string;
            verification: string;
        }

        const param: IDataSaveParam = {
            username: this.formData.account,
            password: this.formData.password,
            verification: this.formData.verification,
        };

        this.loading.show();

        try {
            // let { status, data }: AxiosResponse = await this.axios.post(url, param);
            const status = 200;
            if (status === 200) {
                ServiceToast.success(this.$i18n.ForgotPassword_Step4Title);
                this.nextStep();
            }
        } catch (e) {
            ServiceResponseFilter.catchError(this, e);
        } finally {
            this.loading.hide();
        }
    }
}
