

















































































































































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { IComponent } from '@/../models';

import { EPageAction } from '@/../enums';
import { ServiceRegex } from '@/../helpers';

import { Debounce } from 'lodash-decorators';

enum EFormTypes {
    password = 'password',
    confirmPassword = 'confirmPassword',
    oldPassword = 'oldPassword',
}

interface IFormData {
    password: string;
    confirmPassword: string;
    oldPassword: string;
    isShowPassword: boolean;
    isShowConfirmPassword: boolean;
    isShowOldPassword: boolean;
}

interface IState {
    passwordFormat: boolean;
    confirmPasswordMatch: boolean;
    oldPassword: boolean;
    passwordIsRequired: boolean;
    confirmPasswordMatchIsRequired: boolean;
    passwordState: boolean;
    confirmPasswordMatchState: boolean;
}

interface IPassrordItem {
    // regex
    regex: RegExp;
    regexOld: RegExp;
    oldPasswordIsRequired: string;
    passwordFormatRegexError: string;
    passwordIsRequired: string;
    confirmPasswordMatchError: string;
    confirmPasswordMatchIsRequired: string;

    // disabled button
    passwordDisabledButton: boolean;
    confirmPasswordDisabledButton: boolean;

    isRequired: boolean;
    cols: number;
}

@Component({
    components: {},
})
export class FormPassword extends Vue {
    //#region prop
    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => null,
    })
    private isClearState: boolean;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => true,
    })
    private isRegex: boolean;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => true,
    })
    private isShowTip: boolean;

    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => EPageAction.create,
    })
    private action: EPageAction;

    @Prop({
        type: Object, // Boolean, Number, String, Array, Object
        default: () => undefined,
    })
    private passwordData: IComponent.IPasswordData;
    //#endregion prop

    //#region variables
    private formDataOriginal: IFormData = {
        password: '',
        confirmPassword: '',
        oldPassword: '',
        isShowPassword: false,
        isShowConfirmPassword: false,
        isShowOldPassword: false,
    };
    private formData: IFormData = { ...this.formDataOriginal };
    private formDataRestore: IFormData = { ...this.formDataOriginal };

    private stateOriginal: IState = {
        passwordFormat: null,
        confirmPasswordMatch: null,
        oldPassword: null,
        passwordIsRequired: null,
        confirmPasswordMatchIsRequired: null,
        passwordState: null,
        confirmPasswordMatchState: null,
    };
    private state: IState = { ...this.stateOriginal };

    private passrordItemOriginal: IPassrordItem = {
        regex: null,
        regexOld: null,
        oldPasswordIsRequired: '',
        passwordFormatRegexError: '',
        passwordIsRequired: '',
        confirmPasswordMatchError: '',
        confirmPasswordMatchIsRequired: '',

        passwordDisabledButton: true,
        confirmPasswordDisabledButton: true,

        isRequired: true,
        cols: 6,
    };
    private passwordItem: IPassrordItem = { ...this.passrordItemOriginal };

    private eFormTypes = EFormTypes;
    //#endregion variables

    //#region Computed
    private get _isRegex(): boolean {
        return this.isRegex;
    }

    private get _isShowTip(): boolean {
        return this.isShowTip;
    }

    private get formCreate(): boolean {
        return this.action === EPageAction.create;
    }

    private get formEdit(): boolean {
        return this.action === EPageAction.edit;
    }

    //#endregion Computed

    //#region Watch
    @Watch('isClearState', { immediate: true, deep: true })
    private isClearStateChanged(newVal: boolean, oldVal: boolean) {
        this.formStateClear();
    }

    @Watch('passwordData', { immediate: true, deep: true })
    private passwordDataChanged(newVal: IComponent.IPasswordData, oldVal: IComponent.IPasswordData) {
        if (newVal) {
            this.formData.password = newVal.password;
            this.formData.confirmPassword = newVal.confirmPassword;
            this.formKeyupEventCheckValue(this.passwordItem.regex, EFormTypes.confirmPassword);
            this.formKeyupEventCheckValue(this.passwordItem.regex, EFormTypes.password);
        }
    }

    @Watch('action', { immediate: true })
    private actionChanged(newVal: EPageAction, oldVal: EPageAction) {
        this.passwordItem.isRequired = newVal === EPageAction.create;
        this.passwordItem.cols = newVal === EPageAction.create ? 6 : 4;
        this.formStateClear();
    }
    //#endregion Watch

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {
        this.initData();
        this.formStateClear();
    }
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion Vue Life

    //#region Init Data
    private initData(): void {
        this.initPassrordItem();
    }

    private initPassrordItem(): void {
        this.passwordItem = {
            regex: this._isRegex ? ServiceRegex.strongPassword() : /.|\./,
            regexOld: /.|\./,
            oldPasswordIsRequired: this.$i18n.Error_OldPasswordRequire,
            passwordFormatRegexError: this.$i18n.Error_Format_Password,
            passwordIsRequired: this.$i18n.Login_PasswordRequire,
            confirmPasswordMatchError: this.$i18n.Error_Format_ConfirmPassword,
            confirmPasswordMatchIsRequired: this.$i18n.Error_ConfirmPasswordRequire,

            passwordDisabledButton: true,
            confirmPasswordDisabledButton: true,

            isRequired: true,
            cols: this.action === EPageAction.create ? 6 : 4,
        };
    }
    //#endregion Init Data

    //#region form
    private formStateClear(): void {
        this.state = {
            passwordFormat: null,
            confirmPasswordMatch: null,
            oldPassword: null,
            passwordIsRequired: null,
            confirmPasswordMatchIsRequired: null,
            passwordState: null,
            confirmPasswordMatchState: null,
        };

        this.formData = JSON.parse(JSON.stringify(this.formDataOriginal));
    }

    @Debounce(200)
    private formKeyupEvent(regex: RegExp, column: string): void {
        this.formKeyupEventCheckValue(regex, column);
    }

    private formKeyupEventCheckValue(regex: RegExp, column: string): void {
        let state: IState = this.state;
        let data: IFormData = this.formData;

        switch (column) {
            case EFormTypes.password:
                if (!!data.password) {
                    state.passwordFormat = new RegExp(regex).test(data.password);
                    state.passwordState = new RegExp(regex).test(data.password);
                    state.passwordIsRequired = true;
                    if (data.confirmPassword) {
                        state.confirmPasswordMatch = data.password === data.confirmPassword ? true : false;
                        state.confirmPasswordMatchState = state.confirmPasswordMatch;
                    } else {
                        state.confirmPasswordMatch = null;
                    }
                    this.passwordItem.passwordDisabledButton = state.passwordFormat ? false : true;
                    this.passwordItem.confirmPasswordDisabledButton = state.confirmPasswordMatch ? false : true;
                } else {
                    this.passwordItem.passwordDisabledButton = true;
                    state.passwordFormat = true;
                    state.passwordIsRequired = false;
                    state.passwordState = false;
                    if (data.confirmPassword) {
                        state.confirmPasswordMatch = data.password === data.confirmPassword ? true : false;
                    } else {
                        state.confirmPasswordMatch = null;
                    }
                }

                this.$emit('inputPassword', data.password, this.passwordItem.passwordDisabledButton, 'password');
                this.$emit('inputConfirm', data.confirmPassword, this.passwordItem.confirmPasswordDisabledButton, 'confirmPassword');

                break;
            case EFormTypes.confirmPassword:
                if (data.confirmPassword) {
                    if (data.password) {
                        state.confirmPasswordMatch = data.password === data.confirmPassword ? true : false;
                        state.confirmPasswordMatchState = data.password === data.confirmPassword ? true : false;
                        state.confirmPasswordMatchIsRequired = true;
                    } else {
                        state.confirmPasswordMatch = false;
                        state.confirmPasswordMatchState = false;
                        state.confirmPasswordMatchIsRequired = true;
                    }
                } else {
                    state.confirmPasswordMatch = true;
                    state.confirmPasswordMatchIsRequired = false;
                    state.confirmPasswordMatchState = state.confirmPasswordMatchIsRequired;
                }

                this.passwordItem.confirmPasswordDisabledButton = state.confirmPasswordMatch ? false : true;
                this.$emit('inputConfirm', data.confirmPassword, this.passwordItem.confirmPasswordDisabledButton, 'confirmPassword');

                break;
            case EFormTypes.oldPassword:
                state.oldPassword = data.oldPassword ? true : false;
                this.$emit('inputOldPassword', data.oldPassword, false, 'oldPassword');

                break;
            default:
                break;
        }
    }

    private formClickEye(key: EFormTypes): void {
        switch (key) {
            case EFormTypes.password:
                this.formData.isShowPassword = !this.formData.isShowPassword;
                break;

            case EFormTypes.confirmPassword:
                this.formData.isShowConfirmPassword = !this.formData.isShowConfirmPassword;
                break;

            case EFormTypes.oldPassword:
                this.formData.isShowOldPassword = !this.formData.isShowOldPassword;
                break;

            default:
                break;
        }
    }

    //#endregion form
}
export default FormPassword;
